import Button from "@material-ui/core/Button";
import Styles from "../../styles.css";
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import GitHubIcon from "@material-ui/icons/GitHub";
import InstagramIcon from "@material-ui/icons/Instagram";

function Buttons() {
  return (
    <div>
      <Button
        variant="contained"
        color="primary"
        className="buttons"
        href="https://www.linkedin.com/in/gerald-n-m-3a3681111/"
        startIcon={<LinkedInIcon />}
      >
        LinkedIN
      </Button>

      <Button
        variant="contained"
        color="var(--color-header-bg)"
        className="buttons"
        href="https://github.com/mswger001"
        startIcon={<GitHubIcon />}
      >
        GitHub
      </Button>

      <Button
        variant="contained"
        color="primary"
        className="buttons"
        href="https://www.instagram.com/g.n.maswoswere"
        startIcon={<InstagramIcon />}
      >
        Instagram
      </Button>
    </div>
  );
}

export default Buttons;
